import React from "react";
import styled from "styled-components";

const StyledMenu = styled.svg`
	height: 100%;
	cursor: pointer;

	& rect {
		fill: ${(props) => (props.visible ? "white" : "black")};
		transition: fill 1s, transform 0.2s;
	}

	& rect:first-of-type {
		transform-origin: center;
		transform: translate(
				${(props) => (props.visible ? "0.9rem, 0.8rem" : "0, 0")}
			)
			rotate(${(props) => (props.visible ? "-45" : "0")}deg);
	}

	& rect:nth-of-type(2) {
		transform-origin: left;
		transform: translateX(${(props) => (props.visible ? "-200" : "0")}%);
	}

	& rect:last-of-type {
		transform-origin: center;
		transform: translate(
				${(props) => (props.visible ? "0.9rem, -0.8rem" : "0, 0")}
			)
			rotate(${(props) => (props.visible ? "45" : "0")}deg);
	}
`;

const StyledButton = styled.button`
	height: 100%;
	padding: 0;
	border: 0;
	z-index: 100;
	font-family: inherit;
	font-size: 100%;
	background-color: transparent;
`;

function Menu({ visible, handleClick }) {
	return (
		<StyledButton onClick={handleClick}>
			<StyledMenu
				visible={visible}
				viewBox="0 0 64 44"
				fill="none"
				xmlns="http://www.w3.org/2000/svg"
			>
				<rect width="64" height="8" />
				<rect y="18" width="64" height="8" />
				<rect y="36" width="64" height="8" />
			</StyledMenu>
		</StyledButton>
	);
}

export default Menu;
