import React from "react";
import styled, { keyframes } from "styled-components";

const StyledOverlay = styled.div`
	position: absolute;
	height: ${(props) => (props.visible ? 100 : 0)}vh;
	top: 0;
	left: 0;
	transition: height 0s ${(props) => (props.visible ? 0 : 2)}s;

	& > div {
		background-color: black;
		width: 100vw;
		height: 25vh;
		transition: transform 1s;
		transform: translateX(${(props) => (props.visible ? 0 : -120)}vw);
	}

	& #uno {
		transition-delay: 0s;
	}

	& #dos {
		transition-delay: 0.2s;
	}

	& #tres {
		transition-delay: 0.4s;
	}

	& #cuatro {
		transition-delay: 0.6s;
	}
`;

const NavContenido = styled.nav`
	display: flex;
	flex-direction: column;
	gap: 4rem;
	overflow: hidden;
	align-items: center;
	justify-content: center;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%)
		scale(${(props) => (props.visible ? 100 : 0)}%);
	opacity: ${(props) => (props.visible ? 100 : 0)}%;
	transition: opacity 0.2s ${(props) => (props.visible ? 0.7 : 2)}s,
		transform 0.2s ${(props) => (props.visible ? 0 : 2)}s;

	& > a:nth-of-type(1) > h2 {
		transition-delay: ${(props) => (props.visible ? 0.3 : 0)}s;
	}

	& > a:nth-of-type(2) > h2 {
		transition-delay: ${(props) => (props.visible ? 0.5 : 0.2)}s;
	}

	& > a:nth-of-type(3) > h2 {
		transition-delay: ${(props) => (props.visible ? 0.6 : 0.4)}s;
	}

	& > a:nth-of-type(4) > h2 {
		transition-delay: ${(props) => (props.visible ? 0.7 : 0.5)}s;
	}
`;

const StyledH2 = styled.h2`
	font-style: italic;
	fot-size: 2rem;
	height: ${(props) => (props.visible ? "100" : "0")}%;
	color: white;
	letter-spacing: 1rem;
	transition: letter-spacing 0.2s, transform 0.5s;
	transform: translateX(-${(props) => (props.visible ? 0 : 100)}vw);

	&:hover {
		letter-spacing: 1.2rem;
	}
`;

function Overlay({ visible }) {
	return (
		<StyledOverlay visible={visible}>
			<div id="uno"></div>
			<div id="dos"></div>
			<div id="tres"></div>
			<div id="cuatro"></div>
			<NavContenido visible={visible}>
				<a href="#">
					<StyledH2 visible={visible}>INICIO</StyledH2>
				</a>
				<a href="#">
					<StyledH2 visible={visible}>PROYECTOS</StyledH2>
				</a>
				<a href="#">
					<StyledH2 visible={visible}>CONTACTO</StyledH2>
				</a>
				<a href="#">
					<StyledH2 visible={visible}>RESUMÉ</StyledH2>
				</a>
			</NavContenido>
		</StyledOverlay>
	);
}

export default Overlay;
